export default {
    name: 'app-index',
    props: {
        active: {
            type: String
        },
        theme: Object
    },
    data() {
        return {
            activity_loading: false,
            order_loading: false,
            me_loading: false,
        };
    },
    methods: {
        activityLoad() {
            this.activity_loading = true;
        },
        orderLoad() {
            this.order_loading = true;
        },
        meLoad() {
            this.me_loading = true;
        },
        toActivity() {
            if (this.active === 'activity') {
                return false;
            }
            uni.redirectTo({
                url: '/plugins/community/index/index'
            });
        },
        toOrder() {
            if (this.active === 'order') {
                return false;
            }
            uni.redirectTo({
                url: '/plugins/community/order/order'
            });
        },
        toMe() {
            if(this.active === 'me') {
                return false;
            }
            uni.redirectTo({
                url: '/plugins/community/me/me'
            });
        },
    }
}
